.weather-card {
  border: 1px salmon solid;
  border-radius: 0.5em;
  padding: 1em;
  box-shadow: 5px 5px 5px olive;
  min-width: 10vw;
  margin: 0.5vmin;
}
.weather-card .time {
  font-size: small;
}

.weather-index {
  line-height: 4px;
  font-size: medium;
}
@media screen and (max-width: 1022px) {
  .weather-card {
    width: 40vmin;
    height: 40vmin;
    margin: 2em 0;
    display: inline-block;
  }
}
